.form {
    margin-top: 10px;
    background-color: brown;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgb(86, 84, 87);
    box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
    
    border-radius: 16px;
}

.form div {
    width: 90%;
    border: none;
    border-radius: 4px;
    outline: none;
    font-size: 15px;
    padding: 10px;
    /* margin-left: center;  */
    margin: auto;
    /* background: rgba(255, 255, 255, 0.1); */
    /* color: #fff; */
    letter-spacing: 1px;
    color: black;
}

.form h1 {
    color: white;
    /* margin: 14px 0; */
    /* margin:auto;
    display: block;
    font-size: 22px;
    line-height: 1; */
    padding-left: 10px;
    
}

.form div label{
    color: white;
    /* margin: 14px 0; */
    margin: auto;
    display: block;
    font-size: 20px;
    line-height: 1;
    padding-bottom: 10px;
    padding-top: 10px;
}

.form div input{
    width: 95%;
    border: none;
    border-radius: 4px;
    outline: none;
    font-size: 15px;
    padding: 10px;
    /* margin-left: center;  */
    margin: auto;
    /* background: rgba(255, 255, 255, 0.1); */
    /* color: #fff; */
    letter-spacing: 1px;
    color: black;
}

.form div select {
    width: 100%;
    margin: auto;
    height: 35px;
    border: none;
    font-size: 15px;
    /* padding: 20px; This creates an error and value does not show up.*/
    border-radius: 4px;
}

.form div textarea {
    width: 95%;
    margin: auto;
    border: none;
    outline: none;
    font-size: 15px;
    padding: 10px;
    /* background: rgba(255, 255, 255, 0.1); */
    color: black;
    letter-spacing: 1px;
    border-radius: 4px;
}

.form button {
    margin-top: 30px;
    margin: auto;
    width: 100px;
    height: 50px;
    border-radius: 8px;    
    border: none
}